* {
  box-sizing: border-box;
}

:root {
  --active-color: #00acdc;
  --main-accent: #060b35;
}

body {
  margin: auto;
}

.moyo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: var(--main-accent);
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover {
  color: var(--active-color);
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.nav__item {
  margin-left: 20px;
}

.is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

